import {React, useState} from "react";
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, TextField } from "@material-ui/core";
import "./ItemList.css";
import { getIntValue, setThousandValue } from "../../../../utils/ThousandSeparator";
import ItemCards from "../ItemCards/ItemCards";
import { formatCapitalize } from "../../../../utils/FormatCapitalize";
;

const ItemListComponent = ({itemList, setItemList, goToPeople}) => {
    const [openDialog, setOpenDialog] = useState(false);
    const [itemForm, setItemForm] = useState({
        name: "", price: "", amount: ""
    })
    const [editIdx, setEditIdx] = useState(-1);
    function closeModal(){
        setOpenDialog(false);
    }
    function resetForm(){
        setItemForm({
            name: "",
            price: "",
            amount: ""
        })
    }
    function addProduct(){
        closeModal();
        let newList = [...itemList],
        similarIdx = newList.findIndex(item => 
            item?.price === itemForm.price && item?.name === itemForm.name);
        if(similarIdx < 0){
            newList.push({...itemForm, name: formatCapitalize(itemForm.name)});
        }else{
            let totalAmount = getIntValue(newList[similarIdx].amount)+getIntValue(itemForm.amount);
            newList[similarIdx].amount = setThousandValue(totalAmount.toString());
        }
        setItemList(newList);
        resetForm();
    }
    function editProduct(){
        closeModal();
        let newList = [...itemList];
        newList[editIdx] = itemForm;
        setItemList(newList);
        setEditIdx(-1);
        resetForm();
    }
    function focusOnField(){
        setTimeout(()=>{
            document.getElementById('name-field').focus();
        }, 10)
    }
    return (
        <div>
            <div className="title-wrapper">
                What did You eat?
            </div>
            <div className="button-actions">
                    <Button variant="contained" disabled={itemList.length<1} onClick={()=>{goToPeople(1)}}>Finish</Button>
                    <Button color="primary" variant="contained" onClick={()=>{setOpenDialog(true); focusOnField()}}>Add</Button>
            </div>
            <Dialog open={openDialog} onClose={closeModal}>
                <DialogTitle>Product Details</DialogTitle>
                <DialogContent>
                    <TextField label="Product Name" variant="outlined" id="name-field"
                    fullWidth style={{margin: '10px 0'}} value={(itemForm||{name: ''}).name}
                    onChange={e => setItemForm({...itemForm, name: (e.target.value)})}/>

                    <TextField label="Product Price" variant="outlined"
                    fullWidth style={{margin: '10px 0'}} type={'text'} value={(itemForm||{price:''}).price}
                    onChange={e => setItemForm({...itemForm, price: setThousandValue(e.target.value)})}/>

                    <TextField label="Product Amount" variant="outlined"
                    fullWidth style={{margin: '10px 0'}} type={'text'} value={(itemForm||{amount: ''}).amount}
                    onChange={e => setItemForm({...itemForm, amount: setThousandValue(e.target.value)})}/>
                </DialogContent>
                <DialogActions>
                    <Button onClick={editIdx<0?addProduct:editProduct} disabled={!(itemForm||{name: ''}).name || getIntValue((itemForm||{price:''}).price) < 1 || getIntValue((itemForm||{amount: ''}).amount) < 1} variant="contained" color="primary">
                        {editIdx<0?'Add':'Edit'}
                    </Button>
                </DialogActions>
            </Dialog>

            <ItemCards
                itemList={itemList}
                showAmount={true}
                onClickCard={(idx1)=>{
                    setItemForm(itemList[idx1]);
                    setOpenDialog(true);
                    setEditIdx(idx1)
                }}
                onClickClear={(idx1)=>{
                    let newList = [...itemList];
                    newList.splice(idx1, 1);
                    setItemList(newList);
                }}
            />
        </div>
    )
}

export default ItemListComponent;