import { Button, Tab, Tabs, AppBar, TextField, Dialog, DialogTitle, DialogContent, DialogActions, Select, MenuItem } from "@material-ui/core";
import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { getIntValue, setThousandValue } from "../../../../utils/ThousandSeparator";
import ItemCards from "../ItemCards/ItemCards";
import "./PeopleList.css";
import { formatCapitalize } from "../../../../utils/FormatCapitalize";
import { AlertDialog } from "../AlertDialog/AlertDialog";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    'aria-controls': `scrollable-auto-tabpanel-${index}`,
  };
}

const PeopleList = ({baseItemList=[], onBackAction=()=>{}, onFinishAction=()=>{}}) => {
    //codeline di bawah harus dibenerin karena ini cuma buat sementara aja
    // const constItemList = baseItemList.map(item => ({...item, amount: "0"}));
    const [itemList, setItemList] = useState(baseItemList);
    const [value, setValue] = useState(0);
    const [openDialog, setOpenDialog] = useState(false);
    const [personName, setPersonName] = useState('');
    const [personList, setPersonList] = useState([]);
    const [editIdx, setEditIdx] = useState(-1);
    const [selectedMenuIdx, setSelectedMenuIdx] = useState(-1);
    const [openDialogError, setOpenDialogError] = useState(false);
    const [openDialogConfirm, setOpenDialogConfirm] = useState(false);
    const [confirmMessage, setConfirmMessage] = useState('');
    const handleChange = (_, newValue) => {
      setValue(newValue);
    };
    const disabledFinishButton = () => {
        return itemList.find(item => getIntValue(item.amount) > 0)?true:false;
    }
    function closeModal(){
        setOpenDialog(false);
    }
    function showErrorDialog(){
        setOpenDialogError(true);
        setTimeout(()=>setOpenDialogError(false), 3000);
    }
    function addPerson(){
        closeModal();
        let formatedName = formatCapitalize(personName),
        foundIdx = personList.findIndex(elm => elm.name === formatedName);
        if(foundIdx<0){
            setPersonList(personList.concat([{
                name: formatedName,
                items: []
            }]));
        }else{
            showErrorDialog();
        }
        setPersonName('');
    }
    function editPerson(){
        closeModal();
        let formatedName = formatCapitalize(personName),
        foundIdx = personList.findIndex(elm => elm.name === formatedName);
        if(foundIdx<0){
            let newList = [...personList];
            newList[editIdx] = formatedName;
            setPersonList(newList);
        }else{
            showErrorDialog();
        }
        setPersonName('');
        setEditIdx(-1);
    }
    function removePerson(idx){
        let newList = [...personList],
        personItemList = personList[idx].items||[];
        personItemList.forEach(
            (_, idx2) => {
                deletePersonMenu(idx2, idx);
            }
        )
        newList.splice(idx, 1);
        setPersonList(newList);
        setValue(idx - (idx>0?1:0));
    }
    function focusOnField(){
        setTimeout(()=>{
            document.getElementById('person-name-field').focus()
        }, 10)
    }
    function selectItem(itemIdx, personIdx){
        let personItemList = personList[personIdx].items || [],
        selectedMenu = (itemList||[])[itemIdx] || {},
        newItemList = [...itemList];
        personItemList.push({
            name: selectedMenu.name,
            price: selectedMenu.price
        });
        let intAmount = getIntValue(selectedMenu.amount||'0') - 1;
        newItemList[itemIdx] = {...itemList[itemIdx], amount: setThousandValue(intAmount.toString())}
        setItemList(newItemList);
        setSelectedMenuIdx(-1);
    }
    function deletePersonMenu(itemIdx, personIdx){
        let personItemList = personList[personIdx].items || [],
        newPersonList = [...personList],
        foundItemIdx = itemList.findIndex(item => item.name === personItemList[itemIdx].name),
        newItemList = [...itemList], intAmount = getIntValue(itemList[foundItemIdx].amount) + 1;
        personItemList.splice(itemIdx, 1);
        newPersonList[personIdx] = {
            ...newPersonList[personIdx],
            items: personItemList
        };
        newItemList[foundItemIdx].amount = setThousandValue(intAmount.toString());
        setItemList(newItemList);
        setPersonList(newPersonList);
    }
    return (
        <div>
            <div className="title-wrapper">
                Who was there?
            </div>
            <div className="button-actions">
                    <Button variant="contained" onClick={() => onFinishAction(personList)} disabled={disabledFinishButton()}>Finish</Button>
                    <Button color="primary" variant="contained" onClick={()=>{
                        setConfirmMessage('All Selected Item and Person Will be reset. Are you sure?');
                        setOpenDialogConfirm(true);
                    }}>Back</Button>
                    <Button color="primary" variant="contained" onClick={()=>{setOpenDialog(true); focusOnField();}} disabled={!disabledFinishButton()}>Add</Button>
            </div>

            <Dialog open={openDialog} onClose={closeModal}>
                <DialogTitle>Person</DialogTitle>
                <DialogContent>
                    <TextField label="Person Name" variant="outlined" id="person-name-field"
                    fullWidth style={{margin: '10px 0'}} value={personName}
                    onChange={e => setPersonName(e.target.value)}/>
                </DialogContent>
                <DialogActions>
                    <Button onClick={editIdx<0?addPerson:editPerson} disabled={!personName} variant="contained" color="primary">
                        {editIdx<0?'Add':'Edit'}
                    </Button>
                </DialogActions>
            </Dialog>
            
            <AlertDialog type="error" message="This Person is Already on your List!" openDialog={openDialogError} />
            <AlertDialog showButtons={2} type="confirmation" message={confirmMessage} openDialog={openDialogConfirm}
            onConfirmAction={()=>{
                onBackAction(0);
                setPersonList([]);
                setConfirmMessage('');
                setOpenDialogConfirm(false);
            }}
            onCancelAction={()=>{
                setConfirmMessage('');
                setOpenDialogConfirm(false);
            }} />

            {
                personList.length > 0 &&
                <>
                    <AppBar position="static" color="default">
                        <Tabs
                        value={value}
                        onChange={handleChange}
                        indicatorColor="primary"
                        textColor="primary"
                        variant="scrollable"
                        scrollButtons="auto"
                        aria-label="scrollable auto tabs example"
                        >
                        {
                            personList.map((elm, idx) => {
                                return <Tab key={idx} label={elm.name} {...a11yProps(idx)} />
                            })
                        }
                        </Tabs>
                    </AppBar>
                    <>
                        {
                            personList.map((_, idx) => {
                                return (
                                    <TabPanel key={idx} value={value} index={idx}>
                                        <div className="row">
                                            <div className="col-6">
                                                <Button fullWidth variant="contained"
                                                onClick={()=>removePerson(idx)}>Delete Person</Button>
                                            </div>
                                            <div className="col-6">
                                                <Button color="primary" fullWidth variant="contained"
                                                onClick={()=>{setEditIdx(idx); setPersonName(personList[idx].name); setOpenDialog(true)}}>Edit Person</Button>
                                            </div>
                                        </div>
                                        
                                        <Select
                                            value={selectedMenuIdx}
                                            variant="outlined"
                                            fullWidth
                                            onChange={e=>selectItem(e.target.value, idx)}
                                            >
                                            <MenuItem disabled value={-1}>Select the Menu</MenuItem>
                                            {
                                                itemList.map((item, key)=>{
                                                    return(
                                                        <MenuItem key={key} value={key} disabled={getIntValue(item.amount||'')<1}>{`${item.name} (${item.amount||'0'})`}</MenuItem>
                                                    )
                                                })
                                            }
                                        </Select>
                                        
                                        <div className="cards-wrapper">
                                            <ItemCards itemList={_?.items||[]} onClickCard={()=>{}} onClickClear={idx2=>deletePersonMenu(idx2, idx)} showAmount={false} />
                                        </div>
                                    </TabPanel>
                                )
                            })
                        }
                    </>
                </>
            }
        </div>
    )
}
export default PeopleList;