import { IconButton } from "@material-ui/core";
import "./ItemCards.css";

const ItemCards = ({itemList=[], onClickClear=()=>{}, onClickCard=()=>{}, showAmount=false}) => {
    return (
        <div className="row mobile-margin">
            {
                itemList.map((elm, idx1) => {
                    return (
                        <div key={idx1} className="col-md-3">
                            <div className="item-card">
                                <div className="row">
                                    <div className="item-details" onClick={()=>onClickCard(idx1)}>
                                        <div className="row">
                                            <div className="col-12 pt-10 pb-10">{elm.name}</div>
                                            {
                                                showAmount?
                                                <>
                                                    <div className="col-9 pb-10">Rp.{elm.price}</div>
                                                    <div className="col-3 pb-10">@ {elm.amount}</div>
                                                </>:
                                                    <div className="col-12 pb-10">Rp.{elm.price}</div>
                                            }
                                        </div>
                                    </div>
                                    <div className="item-clear">
                                        <IconButton onClick={()=>onClickClear(idx1)}>
                                            &times;
                                        </IconButton>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                })
            }
        </div>
    )
}

export default ItemCards;