import {React, useState} from "react";
import { DownloadBills } from "./component/DownloadBills/DownloadBills";
import ItemListComponent from "./component/ItemList/ItemList";
import PeopleList from "./component/PeopleList/PeopleList";
import "./SplitBill.css";

export function SplitBill() {
    const [personList, setPersonList] = useState([]);
    const [itemList, setItemList] = useState([
        // {
        //     "name": "Nasi Goreng",
        //     "price": "30,000",
        //     "amount": "3"
        // },
        // {
        //     "name": "Nasi Lemak",
        //     "price": "35,000",
        //     "amount": "2"
        // },
        // {
        //     "name": "Nasi Timbel",
        //     "price": "40,000",
        //     "amount": "3"
        // },
        // {
        //     "name": "Es Teh Manis",
        //     "price": "8,000",
        //     "amount": "5"
        // },
        // {
        //     "name": "Es Jeruk",
        //     "price": "7,000",
        //     "amount": "3"
        // }
    ]);
    const [mode, setMode] = useState(0)
    return (
        <div className="view-wrapper">
            {
                mode<1
                ?<ItemListComponent itemList={itemList} setItemList={setItemList} goToPeople={setMode} />
                :mode<2?<PeopleList baseItemList={itemList} onFinishAction={e => {
                    setPersonList(e);
                    setMode(2);
                }} onBackAction={setMode} />
                :<DownloadBills personList={personList}/>
            }
        </div>
    )
}