import { Button } from "@material-ui/core";
import { useState, useEffect, useRef } from "react"
import exportAsImage from "../../../../utils/exportAsImage";
import { getIntValue, setThousandValue } from "../../../../utils/ThousandSeparator";

export const DownloadBills = ({personList=[{
    name: '',
    items: [{
        name: '',
        price: ''
    }]
}]}) => {
    const [downloadList, setDownloadList] = useState([]);
    const downloadRef = useRef();
    useEffect(()=>{
        let newDownloadList = [];
        personList.forEach(
            person => {
                let totalPrice = 0;
                person.items.forEach(
                    item => {
                        totalPrice += getIntValue(item.price);
                    }
                );
                newDownloadList.push({
                    ...person,
                    totalPrice: setThousandValue(totalPrice.toString())
                })
            }
        )
        setDownloadList(newDownloadList);
    },[personList])
    return (
        <>
            <div className="button-actions">
                    <Button color="primary" variant="contained" onClick={()=>{exportAsImage(downloadRef.current, "Test")}}>Print</Button>
            </div>
            <div className="row" ref={downloadRef}>
                {
                    downloadList.map(
                        (person, idx) => {
                            return(
                                <div key={idx} className="col-md-3">
                                    <div style={{
                                        border: 'solid 2px',
                                        padding: '30px 0',

                                    }}>
                                        <div>{person.name}</div>
                                        {
                                            person.items.map(
                                                item => {
                                                    return (
                                                        <div className="row pl-10 pr-10 pb-10">
                                                            <div className="col-9 text-align-left">{item.name}</div>
                                                            <div className="col-3 text-align-right">{item.price}</div>
                                                        </div>
                                                    )
                                                }
                                            )
                                        }
                                        <div className="row pl-10 pr-10 pt-10" style={{borderTop: 'dashed 1px'}}>
                                            <div className="col-3 text-align-left">Total :</div>
                                            <div className="col-9 text-align-right">{person.totalPrice}</div>
                                        </div>
                                    </div>
                                </div>
                            )
                        }
                    )
                }
            </div>
        </>
    )
}