import { Button, Dialog, DialogActions, DialogContent, DialogTitle } from "@material-ui/core"
import { formatCapitalize } from "../../../../utils/FormatCapitalize"
import "./AlertDialog.css"

export const AlertDialog = ({
    type='',
    message='',
    showButtons=0,
    openDialog=false,
    onConfirmAction = () => {},
    onCancelAction = () => {}
}) => {
    const dialogType = formatCapitalize(type);
    const lowerType = (dialogType==='Confirmation'?'info':dialogType).toLowerCase()
    return (
        <>
            <Dialog open={openDialog}>
                <DialogTitle className={`warning-title ${lowerType}`}>{dialogType}</DialogTitle>
                <DialogContent className="warning-dialog">
                    {message}
                </DialogContent>
                {
                    showButtons>0 && 
                    <DialogActions className="dialog-buttons">
                        {
                            showButtons>1 &&
                            <Button variant="contained" onClick={onCancelAction}>
                                Cancel
                            </Button>
                        }
                        <Button variant="contained" className={lowerType} onClick={onConfirmAction}>
                            Confirm
                        </Button>
                    </DialogActions>
                }
            </Dialog>
        </>
    )
}